<template>
  <div>
    <mrc_ess_iconGroup :is_disabled='readonly'
                       :showReturn='false'
                       @handleEdit='handleEdit'
                       @handleDel='handleDel'></mrc_ess_iconGroup>

    <v-tabs v-model="tab"
            @change='change'
            grow
            background-color="#eeeef0"
            color='primary'
            dark>
      <v-tab v-for="item in items"
             style="color:black"
             :key="item.tab">
        {{ item.tab }}
      </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item>
        <timedTask :disabled='readonly'></timedTask>
      </v-tab-item>

      <v-tab-item>
        <journal :disabled='readonly'></journal>
      </v-tab-item>

    </v-tabs-items>
  </div>
</template>

<script>
import { deleteTimedTask } from '@/service/api/timedTask.js'
import timedTask from './modules/timedTask'
import journal from './modules/journal'
export default {
  components: {
    timedTask,
    journal
  },
  props: {},
  data () {
    return {
      readonly: false,
      tab: null,
      items: [
        { tab: '定时任务设定' },
        { tab: '日志' },
      ],
      list: {}
    };
  },
  computed: {},
  created () { },
  watch: {},
  mounted () {
    this.$route.meta.title = this.$store.getters.subPage_name
    this.readonly = this.$store.state.iconstatus
    this.tab = this.$store.state.tab
  },
  beforeDestroy () {
    this.$store.commit('setTab', '')
  },

  methods: {
    change () {
      this.$store.commit('setTab', this.tab)
    },
    handleEdit () {
      this.readonly = !this.readonly
      this.$store.commit('set_iconstatus', this.readonly)
    },
    handleDel () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
        deleteTimedTask({ uuid: this.$store.state.uuid }).then(res => {
          if (res.code === '200') {
            this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
            this.$router.push({
              name: 'timedTask'
            })
          }
        })
      })
    },
  },
};
</script>

