<template>
  <div>
    <mrc_ess_table :headers='headers'
                   hideAddBtn
                   :paginationOptions='options'
                   @turnPage='getlist'
                   :slotKey="['status']"
                   :isLoading='loading'
                   :items='desserts'>
      <!-- 状态 -->
      <template v-slot:item.status='{item}'>
        <v-btn rounded
               color="#13a671"
               dark
               x-small
               v-if="item.status==1">成功</v-btn>
        <v-btn rounded
               color="#999999"
               dark
               x-small
               v-if="item.status==0">失败</v-btn>
      </template>

    </mrc_ess_table>
  </div>
</template>

