import { getLogList, executeTimedTask } from '@/service/api/timedTask.js'
import moment from 'moment'
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      loading: false,
      menulist: [
        { name: this.$t('label_Edit') },
        { name: this.$t('label_Executed_immediately') },
        { name: this.$t('label_delete') },
      ],
      headers: [
        { text: '日志 ID', align: 'start', value: 'logID', sortable: false },
        { text: this.$t('label_The_name_of_the_task'), align: 'start', value: 'job_name', sortable: false },
        { text: this.$t('label_grouping'), value: 'job_group', sortable: false },
        { text: this.$t('label_Call_target_string'), value: 'invoke_target', sortable: false, },
        { text: '耗时', value: 'job_message', sortable: false, },
        { text: '', value: 'status', sortable: false, },
        { text: '执行时间', value: 'created_utc_datetime', sortable: false, },
      ],
      desserts: [],
    };
  },
  computed: {},
  created () { },
  watch: {
    $route (v) {
      if (v.path == '/timedTask') {
        this.getlist()
      }
    }
  },
  mounted () {
    this.getlist()
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    async getlist (obj) {
      this.loading = true
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await getLogList({
        uuid: this.$store.state.uuid,
        pindex: this.options.cpage,
        psize: this.options.pagesize
      })
      if (res.code == 200) {
        this.desserts = res.data.map((item, index) => {
          item.created_utc_datetime = moment(item.created_utc_datetime).format('YYYY/MM/DD', 'h:mm:ss')
          item.logID = index + 1
          return item
        })
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      // console.log(this.desserts);
      this.loading = false
